<template>
  <div class="main-body">
    <b-alert
      class="login-alert"
      v-model="errorAlert"
      variant="danger"
      dismissible
    >
      <b>Login Failed!</b> User name and/or Password is invalid
    </b-alert>
    <b-alert
      class="login-alert"
      variant="success"
      dismissible
      v-model="successAlert"
      >Logged in successfuly</b-alert
    >
    <div class="login-body">
      <h1 class="login-title">LOGIN</h1>
      <b-form @submit.stop.prevent="onSubmit">
        <b-form-group
          id="email-input-group-1"
          label="Email"
          label-for="email-input-1"
        >
          <b-form-input
            id="email-input-1"
            name="email-input-1"
            type="email"
            v-model="$v.form.email.$model"
            :state="validateState('email')"
            aria-describedby="input-1-live-feedback"
          ></b-form-input>

          <b-form-invalid-feedback id="input-1-live-feedback"
            >enter valid email address</b-form-invalid-feedback
          >
        </b-form-group>
        <b-form-group
          id="password-input-group-1"
          label="Password"
          label-for="password-input-1"
        >
          <b-form-input
            id="password-input-1"
            type="password"
            name="password-input-1"
            v-model="$v.form.password.$model"
            :state="validatePassword('password')"
            aria-describedby="input-1-live-feedback"
          ></b-form-input>

          <b-form-invalid-feedback id="input-1-live-feedback"
            >Required field (min length 8 characters)</b-form-invalid-feedback
          >
        </b-form-group>
        <b-button
          class="submit-button"
          variant="primary"
          type="submit"
          :disabled="disabledButton || $v.form.$invalid"
        >
          Submit
          <b-spinner v-if="loadingButton" small type="grow"></b-spinner>
        </b-button>
      </b-form>
      <div class="subtitle-links">
        <span>Don’t have account?</span>
        <router-link class="register-link" to="/registration"
          >Register Now</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength, email } from 'vuelidate/lib/validators'
import UserDataProvider from '../../services/user'
import { setToken } from '../../services/jwt'
import API from '../../services/api'

export default {
  mixins: [validationMixin],
  data() {
    return {
      form: {
        email: null,
        password: null,
      },
      errorAlert: false,
      successAlert: false,
      loadingButton: false,
      disabledButton: false,
    }
  },
  validations: {
    form: {
      email: {
        required,
        email,
      },
      password: {
        required,
        minLength: minLength(8),
      },
    },
  },
  methods: {
    resetStateFlags() {
      this.errorAlert = false
      this.successAlert = false
      this.loadingButton = false
      this.disabledButton = false
    },
    validateState(email) {
      const { $dirty, $error } = this.$v.form[email]
      return $dirty ? !$error : null
    },
    validatePassword(password) {
      const { $dirty, $error } = this.$v.form[password]
      return $dirty ? !$error : null
    },
    resetForm() {
      this.form = {
        email: null,
        password: null,
      }

      this.$nextTick(() => {
        this.$v.$reset()
      })
    },
    onSubmit() {
      this.resetStateFlags()
      this.$v.form.$touch()
      if (this.$v.form.$anyError) {
        this.errorAlert = true
        return
      }
      this.loadingButton = true
      this.disabledButton = true
      UserDataProvider.login({
        email: this.form.email,
        password: this.form.password,
      })
        .then((response) => {
          setToken(response.data.user.token)
          API.addAuthorizationHeader()
          this.successAlert = true
          setTimeout(() => {
            this.$router.push({ path: '/articles' })
            this.loadingButton = false
            this.disabledButton = false
          }, 2000)
        })
        .catch(() => {
          this.loadingButton = false
          this.disabledButton = false
          this.errorAlert = true
        })
    },
  },
}
</script>

<style scoped>
.main-body {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: left;
}
.login-body {
  background-color: #eceeef;
  min-height: 423px;
  margin: auto;
  padding: 37px 20px 19px;
  border-radius: 4px;
  width: 450px;
}
.login-title {
  font-size: 47px;
  color: #707070;
  text-align: center;
}
.submit-button {
  width: 100%;
  margin-top: 45px;
}
.subtitle-links {
  margin-top: 15px;
  font-size: 16px;
  color: #373a3c;
}
.register-link {
  font-weight: bold;
  color: #373a3c;
  margin-left: 10px;
}
.login-alert {
  position: fixed;
  top: 30px;
  right: 30px;
}
</style>
